<template>
    <div class="card">
        <!--左侧头像-->
      <div class="hard-icon">
        <img :src="icon">
      </div>
      <!--姓名-->
      <div class="name" v-text="name"></div>
      <!--提醒按钮-->
      <div class="remind-btn" v-show="isShowBtn" @click="remindClick" :class="{'gray-text': grayStyle, 'gray': showGrayBtn}" v-text="getBtnStatus(remindText)"></div>
      <!--已查阅信息 时间-->
      <div class="time-left" v-show="this.nrConsult === 1">
        <span>已查阅</span>
        <span>(</span>
        <span>{{lookTime | time}}</span>
        <span>)</span>
      </div>
      <!--已回执时间-->
      <div class="time-right" v-show="receiptTime">{{receiptTime | time}}</div>
    </div>
</template>

<script>
import api from '../utils/api';
export default {
  name: 'publish-card',
  props: {
    id: String, // id
    icon: String, // 头像icon
    name: String, // 姓名
    lookTime: [String, Number], // 已查阅时间
    receiptTime: Number, // 已回执时间
    remindText: Number, // 提醒/已提醒 状态
    isShowBtn: Boolean, // 是否显示提醒按钮
    userType: Number, // 请求参数
    noticeId: Number, // 请求参数
    userId: Number, // 请求参数
    currentReceipt: Number, // 判断当前是需回执 还是不需回执页面
    nrConsult: Number, // 根据状态 判断是否显示已查阅 0 不显示 1显示
    showGrayBtn: Boolean, // 是否置灰提醒按钮
    feedReceiptTime: Number // 父组件名片里显示的回执时间
  },
  data () {
    return {
      grayStyle: false
    };
  },
  methods: {
    // 提醒按钮点击
    remindClick () {
      // 获取当前系统时间戳
      let timestamp = (new Date()).valueOf();
      if (!this.showGrayBtn) {
        if (!this.feedReceiptTime || this.feedReceiptTime === 253402271639000 || (this.feedReceiptTime !== 253402271639000 && this.feedReceiptTime > timestamp)) {
          let params = {
            userType: this.userType,
            noticeId: this.noticeId,
            userId: this.userId
          };
          if (this.remindText === 0) {
            api.remaindSingle(params).then((data) => {
              console.log(data);
              // 更新数据
              this.$emit('updateListData', true);
            }, (err) => {
              console.log(err);
            });
          }
        } else {
          this.$emit('updateListData', false);
          this.getBtnStatus(0);
          // this.showGrayBtn = true;
        }
      }
    },
    /**
     * 获取按钮状态
     * @param remindText
     * @returns {string}
     */
    getBtnStatus (remindText) {
      var str = '';
      if (remindText === 0) {
        str = '提醒';
        this.grayStyle = false;
      } else if (remindText === 1) {
        str = '已提醒';
        this.grayStyle = true;
      }
      return str;
    }
  },
  mounted () {
  }
};
</script>

<style lang="scss" scoped>
.card{
  padding: 13px 15px 13px 0;
  margin-left: 15px;
  width: 100%;
  height: 76px;
  background-color: #fff;
  border-bottom: 1px solid #e1e1e1;/*no*/
  /*头像*/
  .hard-icon{
    float: left;
    width: 50px;
    height: 50px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    overflow: hidden;
    img{
      width: 100%;
      height: 100%;
    }
  }
  /*姓名*/
  .name{
    margin-top: 15px;
    margin-left: 10px;
    float: left;
    font-size: 15px;
    color: #383838;
  }
  /*已查阅时间*/
  .time-left{
    margin-top: 16px;
    margin-right: 10px;
    /*margin-left: 53px;*/
    float: right;
    font-size: 13px;
    color: #9C9C9C;
  }
  /*已回执时间*/
  .time-right{
    margin-top: 16px;
    /*margin-right: 15px;*/
    float: right;
    font-size: 13px;
    color: #9C9C9C;
  }

  /*提示按钮*/
  .remind-btn{
    float: right;
    margin-top: 14px;
    margin-right: 15px;
    width: 48px;
    height: 22px;
    line-height: 22px;
    border: 1px solid #C3303A;/*no*/
    border-radius: 2px;
    text-align: center;
    font-size: 14px;
    color: #C3303A;
    &.gray{
      color: #ACB3BF;
      border: 1px solid #f1f2f3;/*no*/
      background-color: #f1f2f3;
    }
    &.gray-text{
      color: #ACB3BF;
      border: 1px solid #f1f2f3;/*no*/
      background-color: #f1f2f3;
    }
  }
}
</style>



