<template>
  <!--通知详情页-->
  <div class="container-inform-details">
  <div class="container-inform scroll-wrap">
    <!--头部名片区域-->
    <div class="header-card" v-if="receiptData" :class="{'scorePage': receiptData.noType === 2}">
      <v-feed v-if="receiptData"
        :imgUrl = "getIconUrl(receiptData.noType)"
        :titleDetails = "receiptData.noTitle"
        :subtitle = "className"
        :time = "getTime(receiptData.noReceipt,receiptData.noReceiptTime)"
        :receiptInfo="getReceiptInfo(receiptData.noReceipt,receiptData.noReceiptTime)"
        :showRelation="false"
      > </v-feed>
    </div>
    <!--中间文本区域-->
    <div class="content-text"  v-if="receiptData && receiptData.noContent && receiptData.noType !=2">
      <div class="text-wrap" id="wrap" ref="wrap" :class="{'hidden': showMore}">
        <pre class="p-details">{{receiptData.noContent}}</pre>
        <!--图片详情-->
        <div class="details-image" v-if="pictureUrl">
          <!--<img :src="item.npUrl" alt="" v-for="item in pictureUrl">-->
          <div class="img-wrap" v-for="item in pictureUrl">
            <img :src="item.npUrl" alt="">
          </div>
        </div>
      </div>
      <!--查看全部-->
      <div class="more" v-show="hideBtn">
        <span class="more-btn" @click="moreBtnClick" :class="{'rotate': showRotate}">
          {{moreText}}
          <em></em>
        </span>
      </div>
    </div>
    <!--tab栏切换-->
    <div class="tab">
      <div class="tab-btn">
        <span class="btn-first" :class="{'active': firstActive}" @click="tabClick(0)" v-text="getTabfirstText()"></span>
      </div>
      <div class="tab-btn">
        <span class="btn-second" :class="{'active': secondActive}" @click="tabClick(1)" v-text="getTabSecondText()"></span>
      </div>
    </div>
    <!--第一个tab下的名片-->
    <div class="card-area" :class="{'card-first-tab': processedpersonList.length > 0}" v-if="showTabFirst">
    <publish-card  v-if="currentReceipt === 1"
                  v-for="(item,index) in processedpersonList"
                   :key="index"
                  :icon="item.iconUrl"
                  :name="item.name"
                  :receiptTime="item.nrReceiptTime"
                  :isShowBtn="false"
    ></publish-card>
      <publish-card  v-if="currentReceipt === 0"
                     v-for="(item,index) in processedpersonList"
                     :key="index"
                     :icon="item.iconUrl"
                     :name="item.name"
                     :receiptTime="item.nrConsultTime"
                     :isShowBtn="false"
      ></publish-card>
    </div>
    <!--第二个tab下的名片-->
    <div class="card-area" :class="{'card-second-tab': unprocessedPersonList.length > 0}" v-if="showTabSecond">
      <publish-card v-if="currentReceipt === 1"
                    v-for="(itemData,index) in unprocessedPersonList"
                    :key="index"
                    :icon="itemData.iconUrl"
                    :name="itemData.name"
                    :lookTime="itemData.nrConsultTime"
                    :isShowBtn="true"
                    :remindText="itemData.nrRemind"
                    :userType="itemData.nrType"
                    :noticeId="receiptData.noId"
                    :userId="itemData.nrUserId"
                    :currentReceipt="currentReceipt"
                    @updateListData="updateData"
                    :nrConsult="itemData.nrConsult"
                    :showGrayBtn="isShowBtnGray"
                    :feedReceiptTime="receiptData.noReceiptTime"
      ></publish-card>
      <publish-card v-if="currentReceipt === 0"
                    v-for="(itemData,index) in unprocessedPersonList"
                    :key="index"
                    :icon="itemData.iconUrl"
                    :name="itemData.name"
                    :isShowBtn="true"
                    :remindText="itemData.nrRemind"
                    :userType="itemData.nrType"
                    :noticeId="receiptData.noId"
                    :userId="itemData.nrUserId"
                    :currentReceipt="currentReceipt"
                    @updateListData="updateData"
                    :showGrayBtn="isShowBtnGray"
                    :feedReceiptTime="receiptData.noReceiptTime"
      ></publish-card>
    </div>
      <!--暂无数据提示-->
    <div class="no-data-area" v-show="(!processedpersonList.length && actIndex === 0) || (!unprocessedPersonList.length && actIndex === 1)">
      <img src="../assets/icon-no-data.png">
      <p class="p-text">暂无数据</p>
    </div>
    <!--已超时-->
    <div class="timeout" v-show="showTimeout">
      <em></em>
      <p>已超时</p>
    </div>
  </div>
    <!--一键提醒按钮-->
    <div class="remind-area" v-show="(unprocessedPersonList.length && actIndex === 1)">
      <!--按钮-->
      <div class="key-remind-btn" :class="{'gray': showBtnGray}" @click="keyRemindClick">{{keyRemindText}}</div>
    </div>
  </div>
</template>

<script>
import api from '../utils/api';
import vFeed from '../vendor/v-feed';
import publishCard from './publish-card';

export default {
  name: 'inform-details',
  components: {
    vFeed,
    publishCard
  },
  data () {
    return {
      flag: true,
      pictureUrl: [], // 图片详情数组
      showRotate: false,
      moreText: '查看全部',
      actIndex: 0,
      showNoData: false, // 是否显示暂无数据
      showMore: false, // 控制文本区域是否超出隐藏
      hideBtn: true, // 是否显示 查看更多
      firstActive: true, // tab 状态1
      secondActive: false, // tab 状态2
      showTabFirst: true, // 默认显示第一个tab页
      showTabSecond: false, // 显示第二个tab页
      showRemindBtn: false, // 是否显示一键提醒按钮
      receiptData: null, // 回执数据
      className: '', // 班级名称
      processedPersonCount: '', // 已回执或已查阅
      unProcessedPersonCount: '', // 未回执或未查阅
      processedpersonList: [], // 已回执 或 未回执列表
      unprocessedPersonList: [], // 未回执 或 未查阅列表
      currentReceipt: parseInt(this.$route.query.currentReceipt, 10), // 判断当前页  是需要回执的  还是 不需要回执的
      showBtnGray: false, // 是否置灰一键提醒按钮
      keyRemindText: '', // 一键提醒按钮/ 已提醒
      noId: this.$route.query.noId,
      isShowBtnGray: false, // 是否置灰子组件里的提醒按钮
      showTimeout: false
    };
  },
  created () {
    // 页面初始化详情数据
    this.getInformDetail();
  },
  methods: {
  //  是否显示点击更多
    isShowbtn () {
      // 如果是成绩页面不计算高度
      if (this.receiptData.noType === 2) {
        return;
      }
      // let _height = this.$refs.wrap.text.offsetHeight;
      let _wrap = document.getElementById('wrap');
      var _height = _wrap.offsetHeight; // 获取内容高度 根据高度判断 是否展示 查看全部按钮
      console.log(_height);
      setTimeout(() => {
        if (_height > 788) {
          this.showMore = true;
        } else {
          this.hideBtn = false;
        }
      }, 0);
    },
    // 查看更多按钮点击
    moreBtnClick () {
      this.showMore = !this.showMore;
      this.showRotate = !this.showRotate;
      if (this.showMore) {
        this.moreText = '查看全部';
      } else {
        this.moreText = '点击收起';
      }
    },
    /**
     *
     * @param num num为0 代表 tab1 num为1 代表 tab2
     */
    tabClick (num) {
      this.actIndex = num;
      if (num === 0) {
        this.firstActive = true;
        this.secondActive = false;
        this.showTabFirst = true;
        this.showTabSecond = false;
        this.showRemindBtn = false;
      } else {
        this.secondActive = true;
        this.firstActive = false;
        this.showTabSecond = true;
        this.showTabFirst = false;
        this.showRemindBtn = true;
      }
    },
    /**
     * 获取通知详情
     */
    getInformDetail () {
      let params = {
        noticeId: this.noId
      };
      // 无需要回执
      if (this.currentReceipt === 0) {
        api.getWithOutReceipt(params).then((data) => {
          console.log(data);
          this.receiptData = data.data.data.noticeInfo;
          this.className = data.data.data.className;
          this.processedPersonCount = data.data.data.processedPersonCount;
          this.unProcessedPersonCount = data.data.data.unProcessedPersonCount;
          this.processedpersonList = data.data.data.processedpersonList;
          this.unprocessedPersonList = data.data.data.unprocessedPersonList;
          this.pictureUrl = data.data.data.pics;
          console.log(this.processedpersonList);
          // 页面 初始化 判断是一键提醒按钮/ 已提醒按钮
          let remindAll = false;
          for (var i = 0;i < this.unprocessedPersonList.length;i++) {
            if (this.unprocessedPersonList[i].nrRemind === 0) {
              remindAll = true;
            }
          }
          this.keyRemindText = remindAll ? '一键提醒' : '已提醒';
          this.showBtnGray = remindAll ? !!0 : !!1;
          this.$nextTick(() => {
            // 初始化 页面是否显示查看更多
            // 页面进来只执行一次 再次点击提醒刷接口不再计算
            setTimeout(() => {
              if (this.flag) {
                this.isShowbtn();
                this.flag = false;
              }
            }, 50);
          });
        }, (err) => {
          console.log(err);
        });
        // 需回执
      } else if (this.currentReceipt === 1) {
        api.getWithReceipt(params).then((data) => {
          this.receiptData = data.data.data.noticeInfo;
          this.className = data.data.data.className;
          this.processedPersonCount = data.data.data.processedPersonCount;
          this.unProcessedPersonCount = data.data.data.unProcessedPersonCount;
          this.processedpersonList = data.data.data.processedpersonList;
          this.unprocessedPersonList = data.data.data.unprocessedPersonList;
          this.pictureUrl = data.data.data.pics;
          console.log(this.processedpersonList);
          // 页面 初始化 判断是一键提醒按钮/ 已提醒按钮
          let remindAll = false;
          for (var i = 0;i < this.unprocessedPersonList.length;i++) {
            if (this.unprocessedPersonList[i].nrRemind === 0) {
              remindAll = true;
            }
          }
          this.keyRemindText = remindAll ? '一键提醒' : '已提醒';
          this.showBtnGray = remindAll ? !!0 : !!1;
          this.$nextTick(() => {
            // 初始化 页面是否显示查看更多
            // 页面进来只执行一次 再次点击提醒刷接口不再计算
            setTimeout(() => {
              if (this.flag) {
                this.isShowbtn();
                this.flag = false;
              }
            }, 50);
          });
        }, (err) => {
          console.log(err);
        });
      }
    },
    // 获取回执时间
    getTime (noReceipt, noReceiptTime) {
      let [time] = [0];
      if (noReceipt === 0) {
        time = 0;
      } else if (noReceipt === 1 && noReceiptTime && noReceiptTime !== 253402271639000) {
        time = noReceiptTime;
      } else if (noReceipt === 1 && noReceiptTime && noReceiptTime === 253402271639000) {
        time = 0;
      }
      return {time};
    },
    /**
     * 获取头像url 类别
     * @param type
     */
    getIconUrl (type) {
      let url = '';
      if (type === 0) {
        url = require('../assets/icon-tz.png');
      } else if (type === 1) {
        url = require('../assets/icon-zy.png');
      } else {
        url = require('../assets/icon-cj.png');
      }
      return url;
    },
    /**
     * 判断是否需要显示回执状态 （根据回执状态 及回执时间判断）
     * @param receipt
     * @param receiptTime
     */
    getReceiptInfo (receipt, receiptTime) {
      let [flag, receiptImg, setTimeColor] = ['', '', false];
      // 获取当前系统时间戳
      var timestamp = (new Date()).valueOf();
      if (receipt === 0) {
        flag = '-1';
        receiptImg = '';
        setTimeColor = false;
      } else if (receipt === 1 && !receiptTime) {
        flag = '0';
        receiptImg = require('../assets/icon-xhz.png');
        setTimeColor = false;
      } else if (receipt === 1 && receiptTime === 253402271639000) {
        flag = '0';
        receiptImg = require('../assets/icon-xhz.png');
        setTimeColor = false;
      } else if (receipt === 1 && receiptTime && receiptTime !== 253402271639000 && receiptTime > timestamp) {
        flag = '1';
        receiptImg = require('../assets/icon-xshz.png');
        setTimeColor = false;
      } else if (receipt === 1 && receiptTime && receiptTime !== 253402271639000 && receiptTime < timestamp) {
        flag = '0';
        receiptImg = require('../assets/icon-yjz.png');
        setTimeColor = true;
        this.showBtnGray = true; // 置灰一键提醒按钮
        this.isShowBtnGray = true;
      }
      return {flag, receiptImg, setTimeColor};
    },
    /**
     * 获取第一个tab切换显示的文本内容
     */
    getTabfirstText () {
      var str = '';
      // 无需回执
      if (this.currentReceipt === 0) {
        str = `已查阅 (${this.processedPersonCount})`;
      } else if (this.currentReceipt === 1) {
        str = `已回执 (${this.processedPersonCount})`;
      }
      return str;
    },
    /**
     * 获取第二个tab切换显示的文本内容
     */
    getTabSecondText () {
      var str = '';
      // 无需回执
      if (this.currentReceipt === 0) {
        str = `未查阅 (${this.unProcessedPersonCount})`;
      } else if (this.currentReceipt === 1) {
        str = `未回执 (${this.unProcessedPersonCount})`;
      }
      return str;
    },
    /**
     * 根据子组件点击提示状态 重新更新列表数据
     * @param type
     */
    updateData (type) {
      if (type) {
        // this.unprocessedPersonList = [];
        this.getInformDetail();
      } else {
        this.getInformDetail();
        this.timeOutStatus();
      }
    },
    /**
     * 一键提醒
     */
    keyRemindClick () {
      // 获取当前系统时间戳
      let timestamp = (new Date()).valueOf();
      if (!this.showBtnGray) {
        if (!this.receiptData.noReceiptTime || this.receiptData.noReceiptTime === 253402271639000 || (this.receiptData.noReceiptTime !== 253402271639000 && this.receiptData.noReceiptTime > timestamp)) {
          let params = {
            noticeId: this.receiptData.noId
          };
          api.remaindAll(params).then((data) => {
            console.log(data);
            // 更新列表数据
            // this.unprocessedPersonList = [];
            this.getInformDetail();
          }, (err) => {
            console.log(err);
          });
        } else {
          this.getInformDetail();
          this.timeOutStatus();
        }
      }
    },
    /**
     * 已超时状态
     */
    timeOutStatus () {
      this.keyRemindText = '一键提醒';
      this.showBtnGray = true;
      this.showTimeout = true;
      setTimeout(() => {
        this.showTimeout = false;
      }, 500);
    }
  },
  mounted () {}
};
</script>

<style lang="scss">
  .container-inform-details{
    position: relative;
    width: 100%;
    height: 100%;
  }
.container-inform{
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  overflow: scroll;
  background-color: #f2f2f4;
  /*头部名片区域*/
  .header-card{
    padding-left: 15px;
    width: 100%;
    /*height: 89px;*/
    background-color: #fff;
    /*overflow: hidden;*/
    /*覆盖 feed 组件样式*/
    .v-feed{
      /*height: 89px;*/
      /*border-bottom: #e1e1e1 1px solid;!*no*!*/
      .v-feed__con{
        /*padding-top: 15px;*/
      }
      .v-feed__img{
        margin-left: 0;
        margin-top: -3px;
      }
    }
  }
  .scorePage .border-bottom-1px:after,
  .scorePage .border-bottom-1px:before{
    display: none !important;
  }
  pre {
    white-space: pre-wrap;
    word-wrap: break-word;
  }
  .content-text{
    background-color: #fff;
    .text-wrap{
      padding: 12px 15px;
      width: 100%;
      .p-title{
        line-height: 35px;
        font-size: 15px;
        color: #383838;
      }
      .p-details{
        line-height: 25px;
        font-size: 15px;
        color: #383838;
      }
      /*详情图片*/
      .details-image{
        margin-top: 15px;
        width: 100%;
        height: auto;
        .img-wrap {
          position: relative;
          padding-top: 50%;
          overflow: hidden;
          margin-bottom: 15px;
          img {
            width: 100%;
            margin-top: -50%;
          }
        }
      }
      &.hidden{
        height: 258px;
        overflow: hidden;
      }
    }
    /*展开更多*/
    .more{
      width: 100%;
      height: 44px;
      text-align: center;
      .more-btn{
        display: inline-block;
        margin: 0 auto;
        height: 44px;
        line-height: 44px;
        font-size: 15px;
        color: #C3303A;
        em{
          display: inline-block;
          width: 10px;
          height: 6px;
          background: url("../assets/icon-more.png") center no-repeat;
          background-size: 100%;
          vertical-align: middle;
        }
        &.rotate{
          em{
            transform:rotate(180deg);
            -ms-transform:rotate(180deg); 	/* IE 9 */
            -moz-transform:rotate(180deg); 	/* Firefox */
            -webkit-transform:rotate(180deg); /* Safari 和 Chrome */
            -o-transform:rotate(180deg); 	/* Opera */
          }
        }
      }
    }
  }
  /*tab 栏切换*/
  .tab{
    margin-top: 10px;
    width: 100%;
    height: 44px;
    border-bottom: 1px solid #e1e1e1;/*no*/
    background-color: #fff;
    .tab-btn{
      float: left;
      width: 50%;
      height: 44px;
      line-height: 44px;
      font-size: 15px;
      text-align: center;
      color: #777E8C;
      .btn-first{
        position: relative;
        bottom: -1px;
        display: inline-block;
        height: 42px;
        line-height: 42px;
        &.active{
          color: #000;
          border-bottom: 4px solid #C3303A;/*no*/
        }
      }
      .btn-second{
        position: relative;
        bottom: -1px;
        display: inline-block;
        height: 42px;
        line-height: 42px;
        &.active{
          color: #000;
          border-bottom: 4px solid #C3303A;/*no*/
        }
      }
    }
  }
  /*名片区域*/
  .card-area{
    padding-left: 15px;
    background-color: #fff;
    /*overflow: hidden;*/
    &.card-first-tab{
      margin-bottom: 30px;
    }
    &.card-second-tab{
      margin-bottom: 80px;
    }
    .card{
      margin-left: 0;
      border-bottom: 1px solid #e1e1e1;/*no*/
      /*已查阅时间*/
      .time-left{
        /*margin-left: 40px;*/
      }
      .remind-btn{
        margin-right: 0;
      }
    }
    .card:last-child{
      border: none;
    }
  }
  /*暂无数据*/
  .no-data-area{
    flex: 1;
    padding-top: 19px;
    padding-bottom: 45px;
    width: 100%;
    /*height: 178px;*/
    background-color: #fff;
    text-align: center;
    box-sizing: content-box;
    img{
      display: block;
      margin: 0 auto;
      width: 84px;
      height: 84px;
    }
    .p-text{
      margin-top: 14px;
      font-size: 15px;
      color: #606060;
    }
  }
}
  /*一键提交按钮区域*/
  .remind-area{
    position: fixed;
    left: 0;
    bottom: 0;
    margin-top: 27px;
    padding: 8px 30px;
    width: 100%;
    height: 54px;
    background: rgba(255,255,255,0.90);
    box-shadow: 0 0 4px 0 rgba(119,119,119,0.50);
    .key-remind-btn{
      width: 100%;
      height: 38px;
      line-height: 38px;
      text-align: center;
      font-size: 17px;
      color: #fff;
      background: #C3303A;
      border-radius: 4px;
      &.gray{
        color: #fff;
        background: #DDDEE3;
      }
    }
  }
  /*toast 提示*/
  .timeout{
    position: absolute;
    top:50%;
    left: 50%;
    margin-top: -60px;
    margin-left: -60px;
    width: 120px;
    height: 120px;
    background:#000000;
    opacity: 0.5;
    border-radius:5px;
    em{
      margin:15px 0 0 35px;
      /*padding:15px 35px 0 35px;*/
      display: inline-block;
      width:50px;
      height:50px;
      background:url('../assets/icon-timeout.png') no-repeat center;
      background-size:100%;
    }
    p{
      margin-top: 3px;
      font-size: 13px;
      color: #FFFFFF;
      letter-spacing: 0;
      text-align: center;
    }
  }
</style>



